import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../common/context/AuthContext';
import { useUnifiedToast } from '../../common/services/unifiedToastService';
import { UnifiedBackground, UnifiedCard } from "../../common/components/SharedTheme";
import { Banknote, ArrowLeft, Clock, CheckCircle, AlertCircle, Save, X, Info } from 'lucide-react';

function AddBankDetails() {
  const { user } = useAuth();
  const { showSuccess, showError } = useUnifiedToast();
  const navigate = useNavigate();
  const [bankDetails, setBankDetails] = useState({
    account_holder_name: '',
    account_number: '',
    bank_name: '',
    ifsc_code: '',
    branch: ''
  });
  const [loading, setLoading] = useState(true);
  const [submissionStatus, setSubmissionStatus] = useState(null);

  useEffect(() => {
    const fetchBankDetailsAndStatus = async () => {
      if (!user) {
        setLoading(false);
        return;
      }


      // Check Supabase session
      const { data: sessionData, error: sessionError } = await (require('../../common/services/supabaseClient').supabase).auth.getSession();
      if (sessionError) {
      } else {
      }
      try {
        const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

        // 1. Fetch current status
        const statusRes = await fetch(`${apiUrl}/bank-details/status/${user.id}`);
        if (statusRes.ok) {
          const statusData = await statusRes.json();
          setSubmissionStatus(statusData.data);
        }

        // 2. Fetch active bank details
        const { data, error } = await (require('../../common/services/supabaseClient').supabase)
          .from('promoter_bank_details')
          .select('*')
          .eq('promoter_id', user.id)
          .maybeSingle(); // Use maybeSingle() instead of single() to handle no results

        if (error) {
          // Don't throw error, just continue with empty bank details
        } else if (data) {
          setBankDetails({
            account_holder_name: data.account_holder_name || '',
            account_number: data.account_number || '',
            bank_name: data.bank_name || '',
            ifsc_code: data.ifsc_code || '',
            branch: data.branch || ''
          });
        }
      } catch (error) {
      } finally {
        setLoading(false);
      }
    };

    fetchBankDetailsAndStatus();
  }, [user]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setBankDetails(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';

      const payload = {
        promoter_id: user.id,
        promoter_name: user.name || 'Promoter',
        ...bankDetails
      };

      const response = await fetch(`${apiUrl}/bank-details/submit`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload)
      });

      if (!response.ok) {
        const errData = await response.json();
        throw new Error(errData.error || 'Failed to submit bank details');
      }

      showSuccess('Bank details submitted for admin approval!');
      navigate('/promoter/profile');
    } catch (error) {
      showError('Failed to submit: ' + error.message);
    }
  };

  if (loading) {
    return (
      <UnifiedBackground>
        <div className="min-h-screen flex items-center justify-center">
          <div className="animate-spin rounded-full h-12 w-12 border-2 border-orange-500 border-t-transparent"></div>
        </div>
      </UnifiedBackground>
    );
  }

  // If status is Pending, show a status view instead of the form
  if (submissionStatus && submissionStatus.has_submission && submissionStatus.status === 'Pending') {
    return (
      <UnifiedBackground>
        <div className="container mx-auto px-4 pt-48 pb-12 text-center">
          <UnifiedCard className="max-w-2xl mx-auto p-12">
            <div className="w-20 h-20 bg-orange-500/20 rounded-full flex items-center justify-center mx-auto mb-6">
              <Clock className="w-10 h-10 text-orange-400 animate-pulse" />
            </div>
            <h2 className="text-2xl font-bold text-white mb-4">Bank Details Under Review</h2>
            <p className="text-slate-400 text-lg mb-8">
              Your bank account details have been submitted and are currently being reviewed by our administration team.
            </p>
            <div className="bg-slate-800/50 p-4 rounded-xl border border-slate-700 mb-8">
              <p className="text-xs text-slate-500 uppercase tracking-tighter mb-1">Current Status</p>
              <p className="text-xl font-bold text-orange-400 uppercase">{submissionStatus.status}</p>
            </div>
            <button
              onClick={() => navigate('/promoter/profile')}
              className="w-full py-4 bg-slate-700 text-white rounded-xl hover:bg-slate-600 transition-colors font-bold"
            >
              Back to Profile
            </button>
          </UnifiedCard>
        </div>
      </UnifiedBackground>
    );
  }

  return (
    <UnifiedBackground>
      <div className="container mx-auto px-4 pt-32 pb-8">
        <div className="max-w-2xl mx-auto">
          <button onClick={() => navigate(-1)} className="flex items-center text-orange-300 hover:text-orange-100 mb-6 font-semibold transition-colors">
            <ArrowLeft className="w-5 h-5 mr-2" />
            Back to Profile
          </button>

          {submissionStatus?.status === 'Rejected' && (
            <div className="mb-6 bg-red-500/10 border border-red-500/30 p-4 rounded-xl flex items-start">
              <AlertCircle className="w-5 h-5 text-red-500 mr-3 mt-0.5 shrink-0" />
              <div>
                <p className="text-red-500 font-bold mb-1">Previous Submission Rejected</p>
                <p className="text-red-200/80 text-sm">Reason: {submissionStatus.review_notes || 'No reason provided.'}</p>
                <p className="text-red-200/80 text-sm mt-1 italic">Please update the details below and re-submit.</p>
              </div>
            </div>
          )}

          <UnifiedCard>
            <div className="p-8">
              <div className="flex items-center mb-6">
                <div className="w-12 h-12 bg-orange-500/20 rounded-xl flex items-center justify-center mr-4">
                  <Banknote className="w-8 h-8 text-orange-400" />
                </div>
                <div>
                  <h2 className="text-2xl font-bold text-white">Bank Account Details</h2>
                  <p className="text-orange-200/70">Update your withdrawal destination. Admin approval required.</p>
                </div>
              </div>

              <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-orange-300/80 mb-2 uppercase tracking-wider">Account Holder Name</label>
                    <input
                      type="text"
                      name="account_holder_name"
                      value={bankDetails.account_holder_name}
                      onChange={handleChange}
                      placeholder="As per bank records"
                      className="w-full p-4 bg-slate-900/50 border border-slate-700 rounded-xl text-white focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all outline-none"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-orange-300/80 mb-2 uppercase tracking-wider">Account Number</label>
                    <input
                      type="text"
                      name="account_number"
                      value={bankDetails.account_number}
                      onChange={handleChange}
                      placeholder="Enter full account number"
                      className="w-full p-4 bg-slate-900/50 border border-slate-700 rounded-xl text-white focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all outline-none"
                      required
                    />
                  </div>

                  <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                      <label className="block text-sm font-medium text-orange-300/80 mb-2 uppercase tracking-wider">Bank Name</label>
                      <input
                        type="text"
                        name="bank_name"
                        value={bankDetails.bank_name}
                        onChange={handleChange}
                        placeholder="e.g. HDFC, SBI"
                        className="w-full p-4 bg-slate-900/50 border border-slate-700 rounded-xl text-white focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all outline-none"
                        required
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-orange-300/80 mb-2 uppercase tracking-wider">IFSC Code</label>
                      <input
                        type="text"
                        name="ifsc_code"
                        value={bankDetails.ifsc_code}
                        onChange={handleChange}
                        placeholder="11 character code"
                        className="w-full p-4 bg-slate-900/50 border border-slate-700 rounded-xl text-white focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all outline-none"
                        required
                      />
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-orange-300/80 mb-2 uppercase tracking-wider">Branch Name</label>
                    <input
                      type="text"
                      name="branch"
                      value={bankDetails.branch}
                      onChange={handleChange}
                      placeholder="City or Area branch"
                      className="w-full p-4 bg-slate-900/50 border border-slate-700 rounded-xl text-white focus:ring-2 focus:ring-orange-500 focus:border-orange-500 transition-all outline-none"
                      required
                    />
                  </div>
                </div>

                <div className="flex flex-col sm:flex-row justify-end space-y-3 sm:space-y-0 sm:space-x-4 pt-6 border-t border-slate-800">
                  <button
                    type="button"
                    onClick={() => navigate('/promoter/profile')}
                    className="px-8 py-3 text-sm font-semibold text-white bg-slate-800 rounded-xl hover:bg-slate-700 transition-all flex items-center justify-center"
                  >
                    <X className="w-4 h-4 mr-2" />
                    Cancel
                  </button>
                  <button
                    type="submit"
                    className="px-8 py-3 text-sm font-semibold text-white bg-gradient-to-r from-orange-500 to-red-600 rounded-xl hover:shadow-lg hover:shadow-orange-500/20 transition-all flex items-center justify-center"
                  >
                    <Save className="w-4 h-4 mr-2" />
                    Submit for Approval
                  </button>
                </div>
              </form>
            </div>
          </UnifiedCard>

          <div className="mt-8 flex items-start bg-blue-500/10 border border-blue-500/20 p-4 rounded-xl">
            <Info className="w-5 h-5 text-blue-400 mr-3 mt-0.5 shrink-0" />
            <p className="text-blue-200/70 text-sm">
              Note: Changes to bank details will be disabled while a review is pending. Approved details will be used for all future withdrawals.
            </p>
          </div>
        </div>
      </div>
    </UnifiedBackground>
  );
}

export default AddBankDetails;

